export default {
	data() {
		return {
			form: {
				Name: '',
				FAGId: '',
				IsFamily: false,
				IsSelling: false
			},
			rules: {
				Name: {
					required: '',
					message: '请输入商品名称'
				}
			}
		};
	},
	methods: {
		submitForm: function(param) {
			uni.showModal({
				content: '表单数据内容：' + JSON.stringify(param),
				showCancel: false
			});
		}
	}
};
